.login {
  display: flex;
  flex-direction: column;
  max-width: 20vw;
}

.choice {
  display: flex;
  justify-content: space-evenly;
}

#email {
  width: 47%;
}

#company {
  width: 47%;
}

#error {
  color: red;
  font-weight: 900;
}

.form-group {
  padding-right: 3%;
  width: 47%;
}

.form-row {
  width: 100%;
  justify-content: flex-start;
}

.profile-confirm {
  max-height: 200px;
  max-width: 300px;
}

#spinner {
  width: 100%;
  height: 100px;
  display: flex;
}

#spinner svg {
  color: orangered;
}

.show-data {
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         padding: 5px;
         width: 100%;
         align-items: center;
}



.time-left {
  color: orangered;
  font-weight: 900;
}

span {
  margin-left: 4px;
}

.photo-container {
  display: flex;
}

@media only screen and (max-width: 700px) {
  .photo-container {
    flex-direction: column;
  }

  .show-photo {
    text-align: center;
  }
}